.cw-steps {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-inline: 1.25rem;
	gap: 2rem;
	margin-bottom: 0;
	padding: 4rem 0.875rem;
	margin: 0 auto;
	max-width: 320px;
}
.cw-step {
	position: relative;
}
@media (min-width: 1024px) {
	.cw-steps {
		margin: 0 auto;
		padding-inline: 6rem;
		margin-bottom: 0;
		gap: 3.25rem;
		padding: 6.25rem 1rem;
		max-width: 1260px;
	}
}

.cw-steps__text {
	text-align: center;
	max-width: 61.25rem;
}

.cw-steps__text h2 {
	/* font-size: 1.75rem; */
	font-size: 24px;
	font-weight: 700;
	line-height: 1.2;
	color:#000D17;
margin: 0;
margin-bottom: 0.5rem;
}


@media (min-width: 1024px) {
	.cw-steps__text h2 {
		/* font-size: 3.125rem; */
		font-size: 40px;
		margin-bottom: 0.5rem;
	}
}

.cw-steps__text p {
	/* font-size: 1rem; */
	font-size: 16px;
	line-height: 1.4;
	margin: 0;
	font-weight: 700;
	color: #3C4B5F;
}
.cw-steps__text--prev {
	font-style: normal;
	font-weight: 700;
	/* font-size: 1.1rem; */
	font-size: 14px;
	line-height: 1.4;
	letter-spacing: 0.2em;
	text-transform: uppercase;
	color: #DFA01F;
	margin-bottom: 0.5rem;
}
@media (min-width: 1024px) {
	.cw-steps__text p {
		/* font-size: 1.375rem; */
		font-size: 20px;
		font-weight: 700;
	}
	.cw-steps__text--prev {
		/* font-size: 0.9rem; */
	}
}

.cw-steps__steps {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: start;
	width: 100%;
	/* max-width: 96rem; */
	gap: 2.5rem;
	position: relative;
}

@media (min-width: 1024px) {
	.cw-steps__steps {
		flex-direction: row;
		justify-content: space-between;
		/* gap: 3.25rem; */
		gap: 3%;
	}
}

.cw-steps .cw-step {
	display: flex;
	flex-direction: column;
		align-items: center;
	justify-content: center;
	width: 100%;
	gap: 1rem;
}

@media (min-width: 1024px) {
	.cw-step {
	}
}

.cw-steps .cw-step__img {
	flex-shrink: 0;
width: 75px;
	height: 75px;
	margin: 0 auto;
	margin-bottom: 0.5rem;
}

.cw-steps .cw-step__img img {
	width: 100%;
	height: 100%;
	display: block;
	object-fit: contain;
}

/* .cw-step:nth-child(3) .cw-step__img img {
	object-position: right;
} */

@media (min-width: 1024px) {
	.cw-steps .cw-step__img {
	width: 100px;
		height: 100px;
	}
}

.cw-steps .cw-step__content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	/* align-items: center; */
	gap: 0.625rem;
}

.cw-steps .cw-step__title {
	display: flex;
	flex-direction: column;
	gap: 0.625rem;
	justify-content: flex-start;
	align-items: center;

}

@media (min-width: 1024px) {
	.cw-steps .cw-step__title {
		/* flex-direction: row; */
		justify-content: center;
	}
}

.cw-steps .cw-step__number {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
	border-radius: 50%;
	font-size: 16px;
	width: 24px;
	height: 24px;
	background-color: #3C4B5F;
	flex-shrink: 0;
	font-weight: 600;
	position: relative;
	border:2px solid white;
	border-radius:50%;
	position: relative;
	z-index: 10;
	/* font-size: 1.25rem; */
}
.cw-steps .cw-step__number--arrow {
	display: none;
}

@media (min-width: 1024px) {
	.cw-steps .cw-step__number {
		flex-direction: row;
		width: 65px;
		height: 65px;
		/* font-size: 1.25rem; */
		font-size: 25px;
	}
	/*.cw-steps .cw-step__number--arrow {*/
	/*	position: absolute;*/
	/*	left: 40%;*/
	/*	z-index: -1;*/
	/*	display: block;*/
  /*}*/
	.cw-steps__steps::before {
		content:'';
		position: absolute;
		top: 155px;
		left: 50%;
		transform: translate(-50%,50%);
		height: 2px;
		background-color: #0F62A9;
		width: 67%;
		z-index: 1;
	}
	.cw-step:not(:last-child) .cw-step__number::before{
		content: '';
		position: absolute;
		right: -287%;
		top: 50.4%;
		transform: translateY(-50%);
		height: 17px;
		width: 9px;
		background-size: cover; /* или contain */
		background-position: center; /* чтобы центрировать изображение */
		background-repeat: no-repeat; /* чтобы избежать повторения изображения */
		background-image: url("./assets/arrow.svg");
	}
}

.cw-steps .cw-step__title h3 {
	/* font-size: 1.125rem; */
	font-size: 18px;
	font-weight: 700;
	line-height: 1.4;
	text-transform: capitalize;
	margin: 0;
	color: #004C8D;
}
.cw-steps__steps .cw-step__title h3 {
	color: #000D17;
}
@media (min-width: 1024px) {
	.cw-steps .cw-step__title h3 {
		/* font-size: 1.5rem; */
		text-align: center;
		font-size: 25px;
	}
}

.cw-steps .cw-step__content p {
	/* font-size: 1rem; */
	font-size: 14px;
	line-height: 1.4;
	margin: 0;
	text-align: center;
	color:#3C3C3C;
}

@media (min-width: 1024px) {
	.cw-steps .cw-step__content p {
		/* font-size: 1.125rem; */
		font-size: 16px;
		text-align: center;
		padding: 0 30px;
	}
}


